* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@500&family=Jura&family=Rubik+Mono+One&family=Russo+One&display=swap');
body {
  background-color: #FFDF8C;
  padding: 43px 0;
}

.App {
  border-radius: 10px;
  background-color: #fff;
  text-align: center;
  max-width: 1340px;
  padding: 0 60px;
  margin: auto;
  position: relative;
}

.app-wrapper-content {
  height: auto;
}

@media screen and (max-width: 1250px) {
  body {
    padding: 20px 0;
  }
}

@media screen and (max-width: 1000px) {
  .App {
    width: 100%;
    padding: 0 10px;
  }
}

@media screen and (max-width: 820px) {
  body {
    padding: 0;
  }

  .app-wrapper-content {
    position: relative;
  }

  .blured {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    backdrop-filter: blur(4px);
    transition: .5s;
  }
}

@media screen and (max-width: 414px) {
  .App {
    padding: 0;
    border-radius: 0;
  }
}

@media (prefers-color-scheme: dark) {
  html {
    background-color: #181a1b !important;
  }
  html {
      color-scheme: dark !important;
  }
  html, body {
      background-color: #181a1b;
  }
  html, body {
      border-color: #736b5e;
      color: #e8e6e3;
  }
  a {
      color: #3391ff;
  }
  table {
      border-color: #545b5e;
  }
  ::placeholder {
      color: #b2aba1;
  }
  input:-webkit-autofill,
  textarea:-webkit-autofill,
  select:-webkit-autofill {
      background-color: #404400 !important;
      color: #e8e6e3 !important;
  }
  ::-webkit-scrollbar {
      background-color: #202324;
      color: #aba499;
  }
  ::-webkit-scrollbar-thumb {
      background-color: #454a4d;
  }
  ::-webkit-scrollbar-thumb:hover {
      background-color: #575e62;
  }
  ::-webkit-scrollbar-thumb:active {
      background-color: #484e51;
  }
  ::-webkit-scrollbar-corner {
      background-color: #181a1b;
  }
  ::selection {
      background-color: #004daa !important;
      color: #e8e6e3 !important;
  }
  ::-moz-selection {
      background-color: #004daa !important;
      color: #e8e6e3 !important;
  }

  /* Invert Style */
  .jfk-bubble.gtx-bubble, .captcheck_answer_label > input + img, span#closed_text > img[src^="https://www.gstatic.com/images/branding/googlelogo"], span[data-href^="https://www.hcaptcha.com/"] > #icon, #bit-notification-bar-iframe, ::-webkit-calendar-picker-indicator {
      filter: invert(100%) hue-rotate(180deg) contrast(90%) !important;
  }

  /* Variables Style */
  :root {
    --darkreader-neutral-background: #131516;
    --darkreader-neutral-text: #d8d4cf;
    --darkreader-selection-background: #004daa;
    --darkreader-selection-text: #e8e6e3;
  }

  /* Modified CSS */
  body {
      background-color: rgb(90, 65, 0);
  }
  .App {
      background-color: rgb(24, 26, 27);
  }
  .nav_btn {
      text-decoration-color: initial;
      border-color: initial;
  }
  .nav_btn span {
      color: rgb(237, 105, 51);
  }
  .nav_btn:focus {
      outline-color: initial;
      color: rgb(254, 107, 47);
  }
  .basket {
      background-color: rgb(186, 54, 1);
  }
  .basket span {
      color: rgb(232, 230, 227);
  }
  @media screen and (max-width: 820px) {
      .navbar_mobile_container {
          background-color: rgba(175, 6, 37, 0.9);
      }
      .bar1,
      .bar2,
      .bar3 {
          background-color: rgba(175, 6, 37, 0.9);
      }
      .change .bar1 {
          background-color: rgba(24, 26, 27, 0.9);
      }
      .change .bar3 {
          background-color: rgba(24, 26, 27, 0.9);
      }
      .nav_btn_mobile {
          text-decoration-color: initial;
      }
      .nav_btn_mobile span {
          color: rgb(232, 230, 227);
          border-color: initial;
      }
  }
  @media screen and (max-width: 414px) {
      .header_container {
          box-shadow: rgba(27, 48, 64, 0.35) 0px 7px 46px -14px;
      }
      .header_wrapper {
          background-color: rgb(24, 26, 27);
      }
      .navbar_mobile_container {
          background-color: rgba(175, 6, 37, 0.9);
      }
      .nav_btn_mobile:focus {
          background-color: rgb(175, 6, 37);
      }
  }
  .size_item {
      border-color: rgb(58, 62, 65);
  }
  .packing_item {
      border-color: rgb(58, 62, 65);
      background-color: rgb(34, 36, 38);
  }
  .size_item:focus {
      background-color: rgb(34, 36, 38);
  }
  .btn_order {
      background-color: rgb(186, 54, 1);
      color: rgb(232, 230, 227);
  }
  .btn_order:hover {
      color: rgb(232, 230, 227);
      background-color: rgb(148, 42, 0);
  }
  .btn_order:focus {
      border-color: rgb(169, 50, 1);
      background-color: rgb(24, 26, 27);
      color: rgb(254, 107, 47);
  }
  button {
      background-color: rgb(24, 26, 27);
      border-color: rgb(58, 62, 65);
  }
  button:hover {
      background-color: rgb(20, 55, 0);
  }
  button:focus {
      outline-color: initial;
  }
  .order_index {
      background-color: rgb(186, 54, 1);
      color: rgb(232, 230, 227);
  }
  @media screen and (max-width: 500px) {
      .catalog_item {
          border-bottom-color: rgb(62, 68, 70);
      }
      .catalog_item:hover {
          box-shadow: none;
      }
      .size_item {
          border-color: rgb(58, 62, 65);
      }
  }
  * {
      color: rgb(189, 183, 175);
  }
  .TeaCard_card_item__FncfC {
      border-bottom-color: rgb(84, 91, 94);
  }
  @media screen and (max-width: 414px) {
      .TeaCard_card_item__FncfC {
          border-bottom-color: initial;
      }
  }
  .AboutUs_wrapper__2eq1u {
      border-bottom-color: rgb(84, 91, 94);
  }
  h1 {
      color: rgb(248, 52, 88);
  }
  .order {
      color: rgb(232, 230, 227);
      background-image: initial;
      background-color: rgb(147, 108, 1);
      border-color: initial;
  }
  .order:hover {
      background-image: initial;
      background-color: rgb(186, 54, 1);
  }
  @media screen and (max-width: 1600px) {
      h1 {
          color: rgb(248, 52, 88);
      }
  }
  body {
      background-color: rgb(90, 65, 0);
  }
  .cart .content__title svg path {
      stroke: rgb(232, 230, 227);
  }
  .cart__clear span {
      color: rgb(186, 180, 171);
  }
  .cart__clear:hover svg path {
      stroke: rgb(197, 192, 185);
  }
  .cart__clear:hover span {
      color: rgb(197, 192, 185);
  }
  .cart__item-info p {
      color: rgb(160, 152, 139);
  }
  .cart__item-remove .button {
      border-color: rgb(106, 98, 87);
  }
  .cart__item-remove svg path {
      fill: rgb(175, 168, 158);
  }
  .cart__item-remove .button:hover,
  .cart__item-remove .button:active {
      box-shadow: rgb(0, 0, 0) 0px 0px 5px;
  }
  .cart__bottom-details span:last-of-type b {
      color: rgb(255, 174, 26);
  }
  .cart__bottom-buttons .go-back-btn {
      text-decoration-color: initial;
      border-color: rgb(60, 65, 68);
  }
  .cart__bottom-buttons .go-back-btn span {
      color: rgb(204, 200, 193);
      text-decoration-color: initial;
  }
  .cart__bottom-buttons .go-back-btn:hover {
      background-color: rgb(0, 0, 0);
      border-color: rgb(140, 130, 115);
  }
  .cart__bottom-buttons .go-back-btn:hover span {
      color: rgb(232, 230, 227);
  }
  .cart--empty p {
      color: rgb(157, 148, 136);
  }
  .minus .plus {
      color: rgb(237, 105, 51);
  }
  .back {
      color: rgb(204, 200, 193);
  }
  @media screen and (max-width: 414px) {
      .cart_back_btn {
          background-color: rgb(186, 54, 1);
          color: rgb(232, 230, 227);
      }
      .cart__item {
          background-color: rgba(53, 57, 59, 0.15);
      }
      .cart_bottom button {
          background-color: rgb(186, 54, 1);
          color: rgb(232, 230, 227);
      }
  }
  .email-form {
      background-color: rgb(24, 26, 27);
  }
  .email-form-wrapper {
      box-shadow: rgba(0, 0, 0, 0.2) 2px 2px 2px 1px;
      background-color: rgb(24, 26, 27);
  }
  input:focus::placeholder {
      color: transparent;
  }
  .hidden-input {
      background-color: transparent;
      border-color: initial;
  }
  .hidden-input:focus {
      outline-color: initial;
  }
  .inp_valid p {
      color: rgb(255, 26, 26);
  }
  @media screen and (max-width: 414px) {
      .email-form-wrapper {
          box-shadow: none;
      }
      .form-total .order_input {
          border-color: initial;
          background-color: rgba(53, 57, 59, 0.2);
      }
      .order_list_wrapper {
          border-color: rgb(62, 68, 70);
      }
      .form-total button {
          background-color: rgb(186, 54, 1);
          color: rgb(232, 230, 227);
      }
  }
  @media screen and (max-width: 414px) {
      :root {
          --darkreader-bg--clr: #af0625;
      }
      .navigation {
          background-color: rgba(175, 6, 37, 0.8);
      }
      .navigation ul li {
          list-style-image: initial;
      }
      .navigation ul li a .text {
          color: rgb(232, 230, 227);
      }
      .indicator {
          background: var(--darkreader-bg--clr);
      }
      .border_top {
          background-image: initial;
          background-color: rgba(24, 26, 27, 0.7);
      }
  }

  /* Override Style */
  .vimvixen-hint {
      background-color: #7b5300 !important;
      border-color: #d8b013 !important;
      color: #f3e8c8 !important;
  }
  ::placeholder {
      opacity: 0.5 !important;
  }
  #edge-translate-panel-body,
  .MuiTypography-body1 {
      color: var(--darkreader-neutral-text) !important;
  }
  gr-main-header {
      background-color: #0f3a48 !important;
  }
  embed[type="application/pdf"][src="about:blank"] { filter: invert(100%) contrast(90%); }

}