  justify-content: space-between;
  align-items: center;
  transition: .3s;
  margin-top: 10px;
}

.catalog_item_block {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.image {
