      -webkit-transform: translateX(calc(20vw * 0));
              transform: translateX(calc(20vw * 0));
  }

  .navigation ul a:nth-child(2).active ~ .indicator {
      -webkit-transform: translateX(calc(20vw * 1));
              transform: translateX(calc(20vw * 1));
  }

  .navigation ul a:nth-child(3).active ~ .indicator {
      -webkit-transform: translateX(calc(20vw * 2));
              transform: translateX(calc(20vw * 2));
  }

  .navigation ul a:nth-child(4).active ~ .indicator {
      -webkit-transform: translateX(calc(20vw * 3));
              transform: translateX(calc(20vw * 3));
  }

